<template lang="html">
  <div class="">
    <default-head header="排名分规则"></default-head>
    <div class="routes-box" id="wrapper">
      <div class="rules-instruct">
        <div class="">P&nbsp;出家[ 0.30, 10.00 ]</div>
        <div class="">S&nbsp;平均态度分[ 1.00, 5.00 ]&nbsp;&nbsp;权重30%</div>
        <div class="">η&nbsp;电话接通率[ 0, 100% ]&nbsp;&nbsp;权重50%</div>
        <div class="">S总&nbsp;商家评价总分[ 20.00, 100.00 ]</div>
      </div>
      <div class="express-box">
        <div class="line line-top">
          S = [ 0.3 * S1 + 0.7 * S2]
        </div>
        <div class="line line-middle">
          S总 = [ 0.3 * S1 + 0.7 * S2 ] * 20
        </div>
        <div class="line line-foot">
          得分 = ( P + 2.5 * S ) X [ ( η / 2 ) ] + 1 ]
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
export default {
  components: { defaultHead }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.routes-box {
  padding-bottom: 1rem;
  background: @whiteColor;
  .rules-instruct {
    color: @defaultColor;
    font-size: .32rem;
    padding: .5rem .22rem 1rem .22rem;
    line-height: .7rem;
  }
  .express-box {
    font-size: .32rem;
    color: @whiteColor;
    .line {
      line-height: .88rem;
      height: .88rem;
      padding: 0 .22rem;
    }
    .line-top {
      background:@lineTopBg;
    }
    .line-middle {
      background:@lineMiddleBg;
    }
    .line-foot {
      background:@lineFootBg
    }
  }
}
</style>



